import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../providers/auth.service';
import {MatSlideToggleModule} from '@angular/material/slide-toggle';
import {MatFormFieldModule} from '@angular/material/form-field'
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatInputModule} from '@angular/material/input';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatCardContent } from '@angular/material';
import {MatSelectModule} from '@angular/material/select';

import {Referral} from './referal.model'


@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})

export class HomePageComponent implements OnInit {
  public alert_class;
  constructor(private authService: AuthService, private router: Router) {
  }
  
  referral: Referral = new Referral();
  my_referrals:Referral[] = []
  ngOnInit() {
    
  }

  logout() {
    this.authService.logout();
    this.router.navigate(['login']);
  }

  callback = childDataVal => {
    this.my_referrals.push(childDataVal)
  }

  
  onRefer(){
    this.my_referrals = []
    this.authService.senddata(this.referral, this.callback)
  }
}


